export const getPostSubtract = frontmatter => {
  const {
    hot = false, // ÁÉ≠Èó®
    recommended = false,
    isTranslated = false,
    title: _title,
    path,
    totalCount,
  } = frontmatter;

  const showHot = !!hot;
  const showRecommended = !showHot && recommended;
  let title = _title;
  if (isTranslated) {
    title = `[ËØë] ${_title}`;
  }
  if (showHot) {
    title = (
      <>
        <span style={{ marginRight: '4px' }}>üî•</span>
        {title}
      </>
    );
  }
  if (showRecommended) {
    title = (
      <>
        <span style={{ marginRight: '4px' }}>üëç</span>
        {title}
      </>
    );
  }
  const contentUrl = Utils.resolvePageUrl(path);
  const label_viewed = `${formatNumber(totalCount)}ÈòÖ`;

  return {
    title,
    contentUrl,
    label_viewed,
  };
};
